@import url(//fonts.googleapis.com/css?family=Vibur);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Box {
    height: 80px;
    width: 80px;
    border-radius: 1px;
    background-color: #243233;
    -webkit-transition: bakcground-color 0.7s ease;
    transition: bakcground-color 0.7s ease;
  }
  
  .Box-lit {
    background-color: #02def0;
  }
.Board {
    margin: auto;
  }
  
  .Board-table {
    margin: auto;
    border-radius: 3px;
    box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
  }
  
  /* neon sign from codepen: https://codepen.io/nodws/pen/bdjwn */
  html,body{
    height:100%
  }
  
  .Board-title {
    text-align: center;
    width: 65%;
    height: 150px;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
     -webkit-user-select: none;
        -moz-user-select: none;
         -ms-user-select: none;
             user-select: none;
  }
  
  .Board-title b{
    font: 400 10vh "Vibur";
    color: #fee;
    text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #ff4444, 0 0 0.5em #ff4444, 0 0 0.1em #ff4444, 0 10px 3px #000;
  }
  
  .Board-title b span{
    -webkit-animation: blink linear infinite 2s;
            animation: blink linear infinite 2s;
  }
  
  .Board-title b span:nth-of-type(2){
    -webkit-animation: blink linear infinite 3s;
            animation: blink linear infinite 3s;
  }
  
  @-webkit-keyframes blink {
    78% {
      color: inherit;
      text-shadow: inherit;
    }
    79%{
       color: #333;
    }
    80% {
      
      text-shadow: none;
    }
    81% {
      color: inherit;
      text-shadow: inherit;
    }
    82% {
      color: #333;
      text-shadow: none;
    }
    83% {
      color: inherit;
      text-shadow: inherit;
    }
    92% {
      color: #333;
      text-shadow: none;
    }
    92.5% {
      color: inherit;
      text-shadow: inherit;
    }
  }
  
  @keyframes blink {
    78% {
      color: inherit;
      text-shadow: inherit;
    }
    79%{
       color: #333;
    }
    80% {
      
      text-shadow: none;
    }
    81% {
      color: inherit;
      text-shadow: inherit;
    }
    82% {
      color: #333;
      text-shadow: none;
    }
    83% {
      color: inherit;
      text-shadow: inherit;
    }
    92% {
      color: #333;
      text-shadow: none;
    }
    92.5% {
      color: inherit;
      text-shadow: inherit;
    }
  }
.App {
  text-align: center;
  background: #112 url(//images.weserv.nl/?url=i.imgur.com/6QJjYMe.jpg)  center no-repeat;
  margin: 0;
  height: 100vh;
  width: 100vw;
}


